import { NVector3 } from '../../../../../../NJSCore';
import { Pixel } from './Pixel';

export class PixelMap {
    public listPixel: Pixel[] = [];
    public rx: number = 0;
    public ry: number = 0;
    public ddx: number = 0.0;
    public ddy: number = 0.0;
    public v0: NVector3 = NVector3.Origin();
    public v1: NVector3 = NVector3.Origin();
    constructor(v0: NVector3, v1: NVector3, rx: number = 10, ry: number = 10) {
        this.rx = rx;
        this.ry = ry;
        this.ddx = 0.0;
        this.ddy = 0.0;
        this.v0 = v0;
        this.v1 = v1;
        // this.Built();
        this.Init();
    }
    public Init() {
        this.ddx = (this.v1.x - this.v0.x) / (this.rx - 1.0);
        this.ddy = (this.v1.y - this.v0.y) / (this.ry - 1.0);
        console.log('ddx is ' + this.ddx + '. ddy is ' + this.ddx);
        console.log('Pixe Map was executed...');
        this.InitGrid();
    }
    public InitGrid() {
        console.log(this.rx, this.ry);

        for (let j = 0; j < this.ry; ++j) {
            for (let i = 0; i < this.rx; ++i) {
                let x = this.v0.x + i * this.ddx;
                let y = this.v0.y + j * this.ddy;
                let px = new Pixel(this);
                px.i = i; px.j = j;
                px.vec = new NVector3(x, y, 0);
                px.recVec.push(new NVector3(x - this.ddx * 0.5, y - this.ddy * 0.5, 0.0));
                px.recVec.push(new NVector3(x + this.ddx * 0.5, y - this.ddy * 0.5, 0.0));
                px.recVec.push(new NVector3(x + this.ddx * 0.5, y + this.ddy * 0.5, 0.0));
                px.recVec.push(new NVector3(x - this.ddx * 0.5, y + this.ddy * 0.5, 0.0));
                // console.log(px);
                this.listPixel.push(px);
            }
        }
        this.InitGridNeighbor();
    }
    public InitGridNeighbor() {
        for (let j = 0; j < this.ry; ++j) {
            for (let i = 0; i < this.rx; ++i) {
                let k = j * this.rx + i;
                let px0 = this.listPixel[k];
                if (i > 0) { px0.nPix.push(this.listPixel[k - 1]); }
                if (i < this.rx - 1) { px0.nPix.push(this.listPixel[k + 1]); }
                if (j > 0) { px0.nPix.push(this.listPixel[k - this.rx]); }
                if (j < this.ry - 1) { px0.nPix.push(this.listPixel[k + this.rx]); }
                if ((i > 0) && (j < this.ry - 1)) { px0.nPix.push(this.listPixel[k + this.rx - 1]); }
                if ((i > 0) && (j > 0)) { px0.nPix.push(this.listPixel[k - this.rx - 1]); }
                if ((i < this.rx - 1) && (j < this.ry - 1)) { px0.nPix.push(this.listPixel[k + this.rx + 1]); }
                if ((i < this.rx - 1) && (j > 0)) { px0.nPix.push(this.listPixel[k - this.rx + 1]); }
            }
        }
        console.log('done for computing neighbors ');
    }
    public GetPixelAtPoint(pt: NVector3) {
        let pixelMap = this;
        let dp = new NVector3(pt.x - pixelMap.v0.x, pt.y - pixelMap.v0.y, pt.z - pixelMap.v0.z);
        let i = Math.round(dp.x / pixelMap.ddx);
        let j = Math.round(dp.y / pixelMap.ddy);
        if (i >= pixelMap.rx) { i = pixelMap.rx - 1; }
        if (j >= pixelMap.ry) { j = pixelMap.ry - 1; }
        if (i < 0) { i = 0; }
        if (j < 0) { j = 0; }
        return pixelMap.listPixel[j * pixelMap.rx + i];
    }
    ////////////////// event
    public MouseMove(x: number, y: number, z: number) {
        let pixelMap = this;
        console.log('mouseMove x:' + x + ', y:' + y + ', z:' + z);
        let cpt = pixelMap.GetPixelAtPoint(new NVector3(x, y, z));
        console.log(cpt.nPix);
        // VisBufferPointsFromVecs(cpt.ptsCenter, 100);
    }
    public onDocumentMouseMove() {
        // console.log('move');
    }
    public onmouseenter() {
        // console.log('enter');
    }
    ////////////////// visualization
    public CVisLineGrid(ctx: CanvasRenderingContext2D) {
        let pixelMap = this;
        for (let i = 0; i < pixelMap.listPixel.length; ++i) {
            let color = '#ff0000';
            // console.log('d');
            ctx.lineWidth = 0.5;
            ctx.beginPath();
            ctx.moveTo(pixelMap.listPixel[i].recVec[0].x, pixelMap.listPixel[i].recVec[0].y);
            // console.log(pixelMap.listPixel[i].recVec)
            for (let j = 1; j < pixelMap.listPixel[i].recVec.length; ++j) {
                ctx.lineTo(pixelMap.listPixel[i].recVec[j].x, pixelMap.listPixel[i].recVec[j].y);
                // console.log(pixelMap.listPixel[i].nPix[j].vec.x, pixelMap.listPixel[i].nPix[j].vec.y);

            }
            ctx.closePath();
            ctx.strokeStyle = color; // ColorUtility.HexToCanvasColor(color, 1);
            ctx.stroke();
            // ctx.fill();
        }
    }
    public VisMeshGrid() {
        let pixelMap = this;
        for (let i = 0; i < pixelMap.listPixel.length; ++i) {
            // VisMeshRectFromFourVecs(pixelMap.listPixel[i].ptsRectLst);
        }
    }
    public VisCenterPoints(ctx: CanvasRenderingContext2D) {
        let pixelMap = this;
        // let pts: NVector3[] = [];
        ctx.fillStyle = '#0ff00f';
        for (let i = 0; i < pixelMap.listPixel.length; ++i) {
            ctx.beginPath();
            ctx.arc(pixelMap.listPixel[i].vec.x, pixelMap.listPixel[i].vec.y, 5, 0, 3.14 * 2);
            // pts.push(pixelMap.listPixel[i].vec);
            ctx.closePath();
            ctx.fill();
        }
        // VisBufferPointsFromVecs(pts);
    }
    public Render(ctx: CanvasRenderingContext2D) {
        /* */
        this.CVisLineGrid(ctx);
        this.VisCenterPoints(ctx);
    }
}