import { PixelMap } from './PixelMap/PixelMap';
import { NVector3 } from '../../../../../NJSCore';
export class Core {
    pMap: PixelMap;
    constructor() {
        this.pMap = new PixelMap(new NVector3(10, 10), new NVector3(200, 200));
        console.log(this.pMap);
    }
    Render(ctx: CanvasRenderingContext2D) {
        this.pMap.Render(ctx);
    }
    MouseDown(e: MouseEvent) {
        console.log(e, 'core');
    }
    MouseUp(e: MouseEvent) {
        console.log(e, 'core');
    }
    MouseMove(e: MouseEvent) {
        console.log(e, 'core');
    }
    MouseDrag(e: MouseEvent) {
        console.log(e, 'core');
    }
}